.game-over h1 {
  margin-bottom: 30px;
}

.game-over-link {
  display: inline-block;
  position: relative;
  width: 130px;
  padding: 5px;
  margin: 0 5px;
  font-size: 1.2em;
  text-decoration: none;
  text-align: center;
  -webkit-transition: background-color .2s, color .2s;
  -o-transition: background-color .2s, color .2s;
  transition: background-color .2s, color .2s;
}



